@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  font-family: 'Poppins', sans-serif;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: -webkit-linear-gradient(135deg, #fff 10%, #56d8e45e 90%);  
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background: #f0f0f0;
  border-radius: 7px;
  box-shadow: 0 0 8px rgba(0,0,0,0.5);
}

.container header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  font-weight: bold;
}

.form .data{
  margin: 20px 0px 15px 0px;
}

.form .data label {
  font-size: 24px;
  font-weight: 450;
}

.form .data input {
  width: 100%;

  font-size: 20px;
  margin: 10px 0px 0px 0px;
  background: none;
  border: none;
  border-bottom: 1px solid #c0c0c0;
}

.form .data input:focus {
  outline: none;
  border-color: #3498db;
  border-bottom-width: 2px;
}

.btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;  
  margin: 25px 0px 0px 0px;
  border: none;
  color: #fff;
  background: -webkit-linear-gradient(left, #56d8e4 10%, #3498db 90%);  
  border-radius: 2px;  
  box-shadow: inset 0 0 0 0 #56d8e4;
  transition: ease-out .35s;
  font-size: 26px;
  font-weight: 600;  
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
  outline: none;
}

.btn:hover {      
  background: -webkit-linear-gradient(left, #77d7e0bb 10%, #3498dbbe 90%);  
  cursor: pointer;
  color: #00000080;
}

.btn:focus {
  outline: none;
  box-shadow: 0 0 20px #00000080;
}

.red-bg {
  width: 25vw;
  background-color: #77d7e0c9;
}

.red-bg button{
  background-color: #000;
  border: none;
}

.red-bg button:hover{
  color: #00000080;
  transition: ease-out .35s;
}

@media screen and (max-width: 600px) {
  .red-bg {
    width: 50%;
    font-size: 16px;
  }
}

@media screen and (max-width: 300px) {
  .red-bg {
    width: 50%;
    font-weight: 200;
  }
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    /*background: linear-gradient(115deg, #f7edcc 10%, #f0cf57 90%);*/
    background-color: #FFFF;
    display: flex;
    align-items: center;
    justify-content: center;
}

.containerHome {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.navbar {
    position: absolute;
    top: 50%;
    left: 53%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.navbar a {
    cursor: pointer;
    color: #000000;
    text-decoration: none;
    font-size: 1.2em;
    text-transform: uppercase;
    font-weight: 500;
    display: inline-block;
    width: 150px;
    transition: all 0.2s ease-in-out;
}

.navbar a:hover {
    color: #9e068a;
}

.navbar .dot {
    width: 6px;
    height: 6px;
    background: #9e068a;
    border-radius: 50%;
    opacity: 0;
    -webkit-transform: translateX(30px);
    transform: translateX(30px);
    transition: all 0.2s ease-in-out;
}

.navbar a:nth-child(1):hover ~ .dot {
    -webkit-transform: translateX(30px);
    transform: translateX(30px);
    transition: all 0.2s ease-in-out;
    opacity: 1;
}

.navbar a:nth-child(2):hover ~ .dot {
    -webkit-transform: translateX(190px);
    transform: translateX(190px);
    transition: all 0.2s ease-in-out;
    opacity: 1;
}

.navbar a:nth-child(3):hover ~ .dot {
    -webkit-transform: translateX(340px);
    transform: translateX(340px);
    transition: all 0.2s ease-in-out;
    opacity: 1;
}

.navbar a:nth-child(4):hover ~ .dot {
    -webkit-transform: translateX(285px);
    transform: translateX(285px);
    transition: all 0.2s ease-in-out;
    opacity: 1;
}

.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

h1 {
    display: flex;
    width: 100%;
    height: 200%;
}

.formCUTask {
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex-wrap: wrap;
    width: 100%;
    padding: 1.6em;
    box-sizing: border-box;
    border-radius: 7px;
    background-color: #ffe99a;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.07),
                0 2px 4px rgba(0, 0, 0, 0.07),
                0 4px 8px rgba(0, 0, 0, 0.07),
                0 8px 16px rgba(0, 0, 0, 0.07),
                0 16px 32px rgba(0, 0, 0, 0.07),
                0 32px 64px rgba(0, 0, 0, 0.07);
}

.taskStructure {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.labelCUTask {
    margin: 0.3em 0;
    font-size: 1.1rem;
    color: #000000;
}
.btnCUTask{
    display: flex;
    width: 100%;
}
.inputCUTask,
.textareaCUTask {
    width: 100%;
    margin-bottom: 25px;
    padding: 5px;
    box-sizing: border-box;
    border: 0;
    border-bottom: 2px solid #bebed2;
    outline: none;
    background: none;
    font-size: 1.3rem;
    color: #000000;
}

.inputCUTask {
    border: 2px solid #bebed2;
    border-radius: 5px;
    resize: none;
}

.textareaCUTask {
    height: 7em;
    border: 2px solid #bebed2;
    border-radius: 5px;
    resize: none;
}

.deadline, 
.tittle, 
.description {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.btnCUTask {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    height: 100%;
    width: 100%;  
    border: none;
    color: #fff;
    background: -webkit-linear-gradient(left, #56d8e4 10%, #3498db 90%);  
    border-radius: 10px;  
    box-shadow: inset 0 0 0 0 #56d8e4;
    transition: ease-out .35s;
    font-size: 20px;
    font-weight: 450;  
    text-transform: uppercase;
    letter-spacing: 1px;
    cursor: pointer;
    outline: none;
}

.btnCUTask:hover {
    background: -webkit-linear-gradient(left, #77d7e0bb 10%, #3498dbbe 90%);  
    cursor: pointer;
    color: #00000080;          
}

.inputCUTask:focus,
.textareaCUTask:focus {
    outline: none;
    border: 2px solid #000000;
}

.footer {
    cursor: pointer;
    color: #000000;
    text-decoration: none;
    padding: 10px;
    text-transform: uppercase;
    font-weight: 500;
    display: inline-block;
    transition: all 0.2s ease-in-out;
}
.icon {
    color: #000000;
    text-decoration: none;
    font-size: 1.2em;
    padding: 4px;
}
.icon:hover {
    transition: 0.3s;
    color: #9e068a;
}

.header {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.loadTask {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.containerTask {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    padding: 10px;
}

.formTaskList {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    padding: 5px;
    box-sizing: border-box;     
    background-color: #ffe174;
    border-radius: 15px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.07),
                0 2px 4px rgba(0, 0, 0, 0.07),
                0 4px 8px rgba(0, 0, 0, 0.07),
                0 8px 16px rgba(0, 0, 0, 0.07),
                0 16px 32px rgba(0, 0, 0, 0.07),
                0 32px 64px rgba(0, 0, 0, 0.07);
}

.date {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #ffe174;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.colorRed{
    color: #ff0000;
    font-size: 1rem;
    font-style: oblique;
}
.colorGreen{
    color: green;
    font-size: 1rem;
    font-style: oblique;
}

.deadlineTask {
    font-size: 2rem;
    font-style: bold;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.descriptionTask {
    height: 100%;
    width: 100%;
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;    
    background-color: #fae59a;
}

.tittleTask {
    width: 100%;
    display: flex;
    padding: 5px;
    flex-direction: column;
    align-items: flex-start;
    background-color: #ffe791;
}
.taskTittle {
    font-size: 1.3rem;
    font-style: bold;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.btnDeleteTask,
.btnUpdateTask { 
    padding: 8px 12px;
    flex-direction: row;
    box-sizing: border-box;
    border: 2px solid #ffe174;
    border-radius: 15px;
    background-color: #810370;
    font-size: 1.1rem;
    color: #ffffff;
    cursor: pointer;
    transition: 0.3s;
}

.btnDeleteTask {
    background-color: #d40000;
}

.btnUpdateTask {
    background-color: #2da000;
}

.btnDeleteTask:hover {
    border: 2px solid #910202;
    color: #ffffff;      
    transition: 0.3s; 
}

.btnUpdateTask:hover {
    border: 2px solid #206b02;
    color: #ffffff;      
    transition: 0.3s;               
}

.header {
    color: #000000;
    text-decoration: none;
    padding: 10px;
    text-transform: uppercase;
    font-weight: 500;
    transition: all 0.2s ease-in-out;
}
